/**
 * imui.Datepicker - DateInput
 * @author riverhan
 * @data 2016-8-10
 */

import React, { PropTypes } from 'react';
import classnames from 'classnames';
import { formatDateStr } from './dateUtils';
import Icon from '../../icon/index';

class DateInput extends React.Component {
  static propTypes = {
    selected: PropTypes.object,
    onInputClick: PropTypes.func.isRequired,
    dateFormat: PropTypes.string.isRequired,
    showIcon: PropTypes.bool,
    showClean: PropTypes.bool,
    handleClean: PropTypes.func,
    disabled: PropTypes.bool
  };

  static defaultProps = {
    selected: undefined,
    handleClean: () => {}
  };

  constructor(props) {
    super(props);

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.props.onInputClick();
  }

  render() {
    const { selected, placeholder, dateFormat, showIcon, showClean } = this.props;
    const value = selected ? formatDateStr(selected, dateFormat) : '';

    return (
      <div
        className={classnames({
          'im-dp-input--wrap': true,
          disabled: this.props.disabled
        })}
        onClick={this.handleClick}
      >
        <input
          ref="input"
          value={value}
          placeholder={placeholder}
          readOnly
        />
        <div className="im-dp-input--icon">
          {showIcon ? <Icon type="calendar" /> : null}
          {showClean ? <Icon
            type="close"
            onClick={this.props.handleClean}
          /> : null}
        </div>
      </div>
    );
  }
}

export default DateInput;
